<template>
  <div>
    <headerBar :header-data="headerData" />
    <div :class="showRemind ? 'container' : 'container-tab'">
      <!-- 温馨提醒 begin -->
      <div class="remind-holidays" :class="showRemind ? 'hide' : ''">
        <div class="remind-tit">
          <i class="iconfont icontongzhi1" /> 温馨提醒：
        </div>
        <div class="remind-content">
          1、婚假(5天)、产假(4个月)、陪产假(5天)、丧假(5天)为带薪假期，不视为请假，超过标准天数的按事假处理。<br>
          2、如果上午请假，下午正常上班，请在APP上申报下午的工作状态。 3、丧假：
          员工及配偶父母、子女、配偶去世，可集中休一次5个工作日丧假。
        </div>
        <div class="remind-close" @click="showRemindBtn()">
          <i class="iconfont iconguanbi" />
        </div>
      </div>
      <!-- 温馨提醒 end -->
      <div class="container-mini">
        <div class="content">
          <div class="common-main box">
            <div class="holidays-flex">
              <div class="tit">{{ new Date().getFullYear() }}年剩余年假</div>
              <div class="date">{{ synj ? synj : 0 }}天</div>
            </div>
          </div>
          <div class="common-main box">
            <div class="holidays-flex">
              <van-cell
                v-model="yxry"
                title="抄送人"
                is-link
                @click="showXzRy = true"
              />
            </div>
          </div>
        </div>

        <!-- 循环列表 begin -->
        <van-swipe-cell v-for="(item, index) of qjList" :key="index">
          <div class="content">
            <div class="common-main box" @click="qjxx = item">
              <van-cell
                title="请假类型"
                :value="item.qjlx"
                required
                is-link
                @click="showQjlxPopup(item)"
              />
              <van-field
                v-if="item.qjlx === '调休'"
                v-model="item.jbrq"
                rows="1"
                autosize
                label="加班时间"
                required
                type="textarea"
                input-align="right"
                placeholder="请填写对应加班日期（上月或当月非工作日）"
              />
              <van-cell
                v-if="item.qjlx === '陪考假'"
                title="考试类型"
                :value="item.kslxShow"
                required
                is-link
                @click="showKslxPopup(item)"
              />
              <van-cell
                title="开始日期"
                :value="item.date"
                required
                is-link
                @click="showKsrqPopup(item)"
              />
              <van-cell
                title="开始时段"
                :value="item.KssdShow"
                required
                is-link
                @click="showKssdPopup(item)"
              />
              <van-cell
                title="结束日期"
                :value="item.date1"
                required
                is-link
                @click="showJsrqPopup(item)"
              />
              <van-cell
                title="结束时段"
                :value="item.JssdShow"
                required
                is-link
                @click="showJssdPopup(item)"
              />
            </div>
          </div>
          <template #right>
            <div class="sideslip">
              <div class="sideslip-item red">
                <div class="icon" @click="deleteQjxx(index)">
                  <i class="iconfont iconshanchu1" />
                </div>
                删除
              </div>
            </div>
          </template>
        </van-swipe-cell>
        <!-- 循环列表 end -->

        <div class="content">
          <div class="common-main box">
            <van-field
              v-model="qjyy"
              rows="1"
              autosize
              label="请假原因"
              required
              type="textarea"
              input-align="right"
              placeholder="请输入请假原因"
            />
          </div>
        </div>

        <!-- 增加按钮 begin -->
        <div class="foot-btn-white">
          <van-button
            type="info"
            block
            plain
            size="large"
            @click="addQjxx()"
          ><i class="iconfont iconzengjia" /> 增加</van-button>
        </div>
        <!-- 增加按钮 end -->
      </div>

      <van-calendar
        v-model="show"
        :min-date="getMinDate()"
        :max-date="getMaxDate()"
        color="#1890ff"
        @confirm="onConfirm"
      />
      <van-calendar
        v-model="show1"
        :min-date="getMinDate()"
        :max-date="getMaxDate1()"
        color="#1890ff"
        @confirm="onConfirm1"
      />

      <!-- 选择请假类型弹窗 beign -->
      <van-popup
        v-model="QjlxShow"
        position="bottom"
        closeable
        round
        :style="{ height: '35%' }"
        @close="qjlxClose()"
      >
        <div class="common-popup">
          <div class="popup-title">选择请假类型</div>
          <div class="popup-content">
            <div class="select-list-flex">
              <ul>
                <li
                  v-for="(item, index) of qjlxList"
                  :key="index"
                  :class="qjxx.qjlx === item ? 'active' : ''"
                  @click="qjlxClick(item)"
                >
                  {{ item }}
                </li>
              </ul>
            </div>
          </div>
          <div class="foot-btn fixed">
            <ul>
              <li>
                <van-button
                  type="info"
                  round
                  size="large"
                  @click="xzqjlx()"
                >确定</van-button>
              </li>
            </ul>
          </div>
        </div>
      </van-popup>
      <!-- 选择日志分类弹窗 end -->
    </div>
    <div class="common-space" />

    <!-- 底部按钮 begin -->
    <div class="foot-btn fixed">
      <ul>
        <li>
          <van-button type="info" round size="large" @click="submit(2)">{{
            headerData.title === "修改请假信息" ? "修改" : "提交"
          }}</van-button>
        </li>
        <li v-if="headerData.title === '修改请假信息'">
          <van-button
            type="info"
            round
            size="large"
            @click="delet()"
          >删除</van-button>
        </li>
      </ul>
    </div>
    <!-- 底部按钮 end -->

    <!-- 开始时段弹窗 beign -->
    <van-popup v-model="KssdShow" round position="bottom">
      <van-picker
        title="开始时段"
        show-toolbar
        :columns="Kssdcolumns"
        @confirm="onKssdConfirm"
        @cancel="KssdShow = false"
      />
    </van-popup>
    <!-- 开始时段弹窗 end -->

    <!-- 结束时段弹窗 beign -->
    <van-popup v-model="JssdShow" round position="bottom">
      <van-picker
        title="开始时段"
        show-toolbar
        :columns="Kssdcolumns"
        @confirm="onJssdConfirm1"
        @cancel="JssdShow = false"
      />
    </van-popup>
    <!-- 结束时段弹窗 end -->

    <!-- 选择考试类型弹窗 beign -->
    <van-popup v-model="KslxShow" round position="bottom">
      <van-picker
        title="考试类型"
        show-toolbar
        :columns="Kslxcolumns"
        @confirm="onJslxConfirm"
        @cancel="KslxShow = false"
      />
    </van-popup>
    <!-- 选择考试类型弹窗 end -->

    <!-- 选择人员弹出层 beign -->
    <van-popup
      v-model="showXzRy"
      closeable
      position="bottom"
      :style="{ height: '100%' }"
    >
      <div class="content">
        <div class="select-search" style="margin-top:40px">
          <van-search v-model="searchVal" placeholder="请输入搜索关键词" />
          <van-button
            plain
            type="info"
            size="small"
            @click="searchRy()"
          >搜索</van-button>
        </div>
        <div v-for="(item, xsIndex) in rylist" :key="xsIndex">
          <div
            class="select-renyuan"
            :class="{ active: item.isActive }"
            @click="selectRy(item)"
          >
            <div class="renyuan-l">
              <div class="icon">
                <i class="iconfont iconxuanzhong" />
              </div>
            </div>
            <div class="renyuan-m">
              <div class="name">{{ item.rymc }}</div>
            </div>
          </div>
        </div>
      </div>

      <div class="common-space" />
      <!-- 底部按钮 begin -->
      <div class="foot-btn fixed">
        <ul>
          <li>
            <van-button
              type="info"
              round
              size="large"
              @click="xzMutilActorsOnConfirm"
            >确定</van-button>
          </li>
        </ul>
      </div>
      <!-- 底部按钮 end -->
    </van-popup>
    <!-- 选择人员弹出层 end -->
  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import { Dialog } from 'vant'
export default {
  name: 'ZjQjxx',
  components: {
    headerBar
  },
  data() {
    return {
      JslxShow: false,
      KslxShow: false,
      searchVal: '',
      yxryids: '',
      yxry: '',
      yxryList: [],
      showXzRy: false,
      qjyy: '',
      qjxx: {},
      qjList: [
        {
          id: '',
          qjlx: '请选择请假类型',
          date: '请选择开始日期',
          kslx: '',
          kslxShow: '请选择考试类型',
          date1: '请选择结束日期',
          KssdShow: '请选择开始时段',
          JssdShow: '请选择结束时段',
          qjksrq: '',
          qjjsrq: ''
        }
      ],
      qjlxList: [],
      synj: '',
      headerData: {
        title: '新增请假信息',
        leftUrl: 'back',
        leftText: ' '
      },
      Kssdcolumns: ['上午', '下午'],
      Kslxcolumns: ['高考', '中考'],
      kslxMap: {
        '高考': 1,
        '中考': 0
      },
      kslxMap1: {
        1: '高考',
        0: '中考'
      },
      show: false,
      show1: false,
      QjlxShow: false,
      KssdShow: false,
      JssdShow: false,
      qjlxMap: {
        事假: 'BSJ',
        年假: 'NJ',
        婚假: 'HJ',
        产假: 'CJ',
        陪产假: 'PCJ',
        丧假: 'SJ',
        调休: 'TX',
        陪考假: 'PKJ'
      },
      qjlxMap1: {
        BSJ: '事假',
        NJ: '年假',
        HJ: '婚假',
        CJ: '产假',
        PCJ: '陪产假',
        SJ: '丧假',
        TX: '调休',
        PKJ: '陪考假'
      },
      buttonState: true,
      minDate: new Date(new Date().getFullYear() - 1, 0, 1),
      maxDate: new Date(new Date().getFullYear() + 10, 0, 1),
      minDate1: new Date(),
      maxDate1: new Date(new Date().getFullYear() + 10, 0, 1),
      showRemind: false,
      rylist: []
    }
  },
  created() {
    this.xb = this.$store.state.mh.mhUserInfo.xb
    this.qjlxList =
      this.xb === 1
        ? ['年假', '事假', '婚假', '陪产假', '丧假', '调休', '陪考假']
        : ['年假', '事假', '婚假', '产假', '丧假', '调休', '陪考假']
    if (this.$route.params.qjxx) {
      this.headerData.title = this.$route.params.title
      this.id = this.$route.params.qjxx.id
      this.getQjxx()
    } else {
      this.qjList[0].KssdShow = '上午'
      this.qjList[0].JssdShow = '下午'
      this.qjList[0].qjkssjd = 1
      this.qjList[0].qjjssjd = 2
    }
    this.getSynj()
    this.getRYxx()
  },
  methods: {
    searchRy() {
      this.getRYxx()
    },
    selectRy(item) {
      item.isActive = !item.isActive
      if (item.isActive) {
        this.yxryList.push(item)
      } else {
        for (let i = 0; i < this.yxryList.length; i++) {
          if (this.yxryList[i].ryid === item.ryid) {
            this.yxryList.splice(i, 1)
          }
        }
      }
    },
    xzMutilActorsOnConfirm() {
      this.yxry = ''
      this.yxryids = ''
      for (let i = 0; i < this.yxryList.length; i++) {
        if (i === this.yxryList.length - 1) {
          this.yxry += this.yxryList[i].rymc
          this.yxryids += this.yxryList[i].ryid
        } else {
          this.yxry += this.yxryList[i].rymc + ';'
          this.yxryids += this.yxryList[i].ryid + ';'
        }
      }
      this.showXzRy = false
    },
    getRYxx() {
      this.$store
        .dispatch('erp/getRYxxAction', {
          rymc: this.searchVal
        })
        .then((res) => {
          this.rylist = res.data
          for (let i = 0; i < this.rylist.length; i++) {
            this.$set(this.rylist[i], 'isActive', false)
          }
          console.log('this.rylist================', this.rylist)
        })
    },
    xzqjlx() {
      this.QjlxShow = false
      if (this.qjxx.qjlx === '丧假') {
        Dialog.confirm({
          title: '提示',
          message:
            '丧假： 员工及配偶父母、子女、配偶去世，可集中休一次5个工作日丧假。'
        })
          .then(() => {})
          .catch(() => {
            this.qjxx.qjlx = '请选择请假类型'
          })
      }
      if (this.qjxx.qjlx === '产假') {
        Dialog.confirm({
          title: '提示',
          message: '本人已知悉并接受公司关于产假期与产假期待遇的相关规定。'
        })
          .then(() => {})
          .catch(() => {
            this.qjxx.qjlx = '请选择请假类型'
          })
      }
    },
    delet() {
      this.$store
        .dispatch('erp/deleteQjxxAction', {
          id: this.id
        })
        .then((res) => {
          this.$toast('删除成功！')
          this.$router.back()
        })
        .catch((error) => [this.$toast(error)])
    },
    qjlxClose() {
      if (
        this.qjxx.qjlx === '婚假' ||
        this.qjxx.qjlx === '陪产假' ||
        this.qjxx.qjlx === '丧假' ||
        this.qjxx.qjlx === '产假'
      ) {
        if (this.qjxx.qjksrq && this.qjxx.qjkssjd) {
          this.getJssj()
        }
      }
    },
    getQjxx() {
      this.$store
        .dispatch('erp/getQjxxByIdAction', {
          id: this.id
        })
        .then((res) => {
          if (res.qjmxes) {
            this.qjList = []
            for (let i = 0; i < res.qjmxes.length; i++) {
              res.qjmxes[i].date = this.formatDate2(res.qjmxes[i].qjksrq)
              res.qjmxes[i].date1 = this.formatDate2(res.qjmxes[i].qjjsrq)
              res.qjmxes[i].KssdShow =
                res.qjmxes[i].qjkssjd === 1 ? '上午' : '下午'
              res.qjmxes[i].JssdShow =
                res.qjmxes[i].qjjssjd === 1 ? '上午' : '下午'
              res.qjmxes[i].qjlx = this.qjlxMap1[res.qjmxes[i].qjlx]
              res.qjmxes[i].kslxShow = this.kslxMap1[res.qjmxes[i].kslx]
              res.qjmxes[i].kslx = res.qjmxes[i].kslx
            }
            this.qjList = res.qjmxes
          }
          this.qjyy = res.qjyy
        })
        .catch((error) => {
          console.log(error)
        })
    },
    submit(type) {
      if (!this.buttonState) {
        return
      }
      if (this.qjyy === '') {
        this.$toast('请填写请假原因！')
        return
      }
      const qjmxs = []
      for (let i = 0; i < this.qjList.length; i++) {
        if (
          this.qjList[i].qjlx === '请选择请假类型' ||
          this.qjList[i].date === '请选择开始日期' ||
          this.qjList[i].date1 === '请选择开始日期' ||
          this.qjList[i].KssdShow === '请选择开始时段' ||
          this.qjList[i].JssdShow === '请选择结束时段' ||
          (this.qjList[i].qjlx === '调休' && !this.qjList[i].jbrq)
        ) {
          this.$toast('请填写请假信息！')
          return
        }
        if (this.qjList[i].qjksrq > this.qjList[i].qjjsrq) {
          this.$toast('开始时间不能大于结束时间！')
          return
        }
        qjmxs.push({
          id: this.qjList[i].id,
          qjlx: this.qjlxMap[this.qjList[i].qjlx],
          jbrq: this.qjList[i].jbrq,
          kslx: this.qjList[i].kslx,
          qjksrq: this.qjList[i].qjksrq,
          qjjsrq: this.qjList[i].qjjsrq,
          qjkssjd: this.qjList[i].qjkssjd,
          qjjssjd: this.qjList[i].qjjssjd
        })
      }
      this.buttonState = false
      console.log('参数=======', {
        id: this.id,
        sqrbh: this.$store.state.mh.mhUserInfo.id,
        sqrmc: this.$store.state.mh.mhUserInfo.name,
        qjyy: this.qjyy,
        qjmxes: qjmxs,
        type: type,
        xmfzr: this.yxryids,
        xmfzrmc: this.yxry
      })
      this.$store
        .dispatch('erp/saveQjxxAction', {
          id: this.id,
          sqrbh: this.$store.state.mh.mhUserInfo.id,
          sqrmc: this.$store.state.mh.mhUserInfo.name,
          qjyy: this.qjyy,
          qjmxes: qjmxs,
          type: type,
          xmfzr: this.yxryids,
          xmfzrmc: this.yxry
        })
        .then((res) => {
          if (type === 1) {
            this.$toast('保存成功！')
          } else {
            this.$toast('提交成功！')
          }
          this.buttonState = true
          this.$router.back()
        })
        .catch((error) => {
          this.$toast(error)
          this.buttonState = true
          console.log(error)
        })
    },
    deleteQjxx(index) {
      this.qjList.splice(index, 1)
    },
    addQjxx() {
      this.qjList.push({
        id: '',
        qjyy: '',
        qjlx: '请选择请假类型',
        date: '请选择开始日期',
        date1: '请选择开始日期',
        KssdShow: '上午',
        JssdShow: '下午',
        qjksrq: '',
        qjkssjd: 1,
        qjjssjd: 2,
        qjjsrq: ''
      })
    },
    qjlxClick(qjlx) {
      if (qjlx === '年假' && this.synj <= 0) {
        this.$toast('已无剩余年假！')
        return
      }
      this.qjxx.qjlx = qjlx
    },
    getSynj() {
      this.$store
        .dispatch('erp/getSynjAction', {
          ygbh: this.$store.state.mh.mhUserInfo.id,
          nf: ''
        })
        .then((res) => {
          this.synj = res
        })
        .catch((error) => {
          console.log(error)
        })
    },
    formatDate(date) {
      let month = ''
      let day = ''
      if (date.getMonth() + 1 < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + date.getDate()
      } else {
        day = date.getDate()
      }
      return `${date.getFullYear()}/${month}/${day}`
    },
    formatDate1(date) {
      const year = date.getFullYear()
      let month = ''
      let day = ''
      if (date.getMonth() + 1 < 10) {
        month = '0' + (date.getMonth() + 1)
      } else {
        month = date.getMonth() + 1
      }
      if (date.getDate() < 10) {
        day = '0' + date.getDate()
      } else {
        day = date.getDate()
      }
      return year + '' + month + day
    },
    formatDate2(strDate) {
      strDate = strDate + ''
      return (
        strDate.slice(0, 4) +
        '/' +
        strDate.slice(4, 6) +
        '/' +
        strDate.slice(6, 8)
      )
    },
    onKssdConfirm(KssdShow) {
      this.KssdShow = false
      this.qjxx.KssdShow = KssdShow
      this.qjxx.qjkssjd = KssdShow === '上午' ? 1 : 2
      if (
        this.qjxx.qjlx === '婚假' ||
        this.qjxx.qjlx === '陪产假' ||
        this.qjxx.qjlx === '丧假' ||
        this.qjxx.qjlx === '产假'
      ) {
        if (this.qjxx.qjksrq) {
          this.getJssj()
        }
      }
    },
    onJssdConfirm1(JssdShow) {
      this.JssdShow = false
      this.qjxx.JssdShow = JssdShow
      this.qjxx.qjjssjd = JssdShow === '上午' ? 1 : 2
    },
    onJslxConfirm(value) {
      this.KslxShow = false
      this.qjxx.kslxShow = value
      this.qjxx.kslx = this.kslxMap[value]
      this.qjxx.qjksrq = ''
      this.qjxx.qjjsrq = ''
      this.qjxx.date = '请选择开始日期'
      this.qjxx.date1 = '请选择结束日期'
      console.log('11=======', this.qjxx)
    },
    getMinDate() {
      if (this.qjxx.kslx === 1) {
        return new Date(new Date().getFullYear(), 5, 6)
      }
      return this.minDate
    },
    getMaxDate1() {
      if (this.qjxx.kslx === 1 && this.qjxx.date && this.qjxx.date !== '请选择开始日期') {
        if (new Date(this.qjxx.date).getDate() + 2 > 8) {
          return new Date(new Date().getFullYear(), 5, 8)
        } else {
          return new Date(new Date().getFullYear(), 5, new Date(this.qjxx.date).getDate() + 1)
        }
      }
      if (this.qjxx.kslx === 0 && this.qjxx.qjksrq) {
        return new Date(new Date(this.qjxx.date).getFullYear(), new Date(this.qjxx.date).getMonth(), new Date(this.qjxx.date).getDate() + 1)
      }
      return new Date(new Date().getFullYear() + 10, new Date().getMonth(), new Date().getDate())
    },
    getMaxDate() {
      if (this.qjxx.kslx === 1) {
        return new Date(new Date().getFullYear(), 5, 8)
      }
      return new Date(new Date().getFullYear() + 10, new Date().getMonth(), new Date().getDate())
    },
    // 选择请假类型
    showQjlxPopup(item) {
      this.qjxx = item
      if(this.synj && this.synj>0){
        this.qjxx.qjlx = '年假'
      }else{
        this.qjxx.qjlx = '事假'
      }
      this.QjlxShow = true
    },
    showKsrqPopup(item) {
      this.qjxx = item
      this.show = true
    },
    showKslxPopup(item) {
      this.qjxx = item
      this.KslxShow = true
    },
    showKssdPopup(item) {
      this.qjxx = item
      this.KssdShow = true
    },
    showJsrqPopup(item) {
      if (
        item.qjlx === '婚假' ||
        item.qjlx === '产假' ||
        item.qjlx === '陪产假' ||
        item.qjlx === '丧假'
      ) {
        return
      }
      this.qjxx = item
      this.show1 = true
    },
    showJssdPopup(item) {
      if (
        item.qjlx === '婚假' ||
        item.qjlx === '产假' ||
        item.qjlx === '陪产假' ||
        item.qjlx === '丧假'
      ) {
        return
      }
      this.qjxx = item
      this.JssdShow = true
    },
    onConfirm(date) {
      this.qjxx.qjksrq = this.formatDate1(date)
      this.qjxx.date = this.formatDate(date)
      if (
        this.qjxx.qjlx === '婚假' ||
        this.qjxx.qjlx === '陪产假' ||
        this.qjxx.qjlx === '丧假' ||
        this.qjxx.qjlx === '产假'
      ) {
        if (this.qjxx.qjkssjd) {
          this.getJssj()
        }
      }
      this.show = false
    },
    getJssj() {
      this.$store
        .dispatch('erp/getQjjsrqAction', {
          qjlx: this.qjlxMap[this.qjxx.qjlx],
          qjksrq: this.qjxx.qjksrq,
          qjkssjd: this.qjxx.qjkssjd
        })
        .then((res) => {
          if (res) {
            if (res.jqjzrq) {
              this.qjxx.qjjsrq = res.jqjzrq
              this.qjxx.date1 = this.formatDate2(res.jqjzrq)
            }
            if (res.jqjzsjd) {
              this.qjxx.JssdShow = res.jqjzsjd === 1 ? '上午' : '下午'
              this.qjxx.qjjssjd = res.jqjzsjd
            }
          }
        })
    },
    onConfirm1(date) {
      this.qjxx.qjjsrq = this.formatDate1(date)
      this.qjxx.date1 = this.formatDate(date)
      this.show1 = false
    },
    showRemindBtn() {
      this.showRemind = !this.showRemind
    }
  }
}
</script>

<style scoped>
.holidays-flex {
  padding: 0 5px;
}
.van-cell::after {
  display: none;
}
</style>
